@mixin box-sizing($boxSize) {
    -webkit-box-sizing: $boxSize;
    -moz-box-sizing: $boxSize;
    box-sizing: $boxSize;
}

@mixin translate($translate-x, $translate-y) {
    -webkit-transform: translate($translate-x, $translate-y);
    -moz-transform: translate($translate-x, $translate-y);
    -ms-transform: translate($translate-x, $translate-y);
    -o-transform: translate($translate-x, $translate-y);
    transform: translate($translate-x, $translate-y);
}

@mixin translateX($translate-x) {
    -webkit-transform: translateX($translate-x);
    -moz-transform: translateX($translate-x);
    -ms-transform: translateX($translate-x);
    -o-transform: translateX($translate-x);
    transform: translateX($translate-x);
}

@mixin translateY($translate-y) {
    -webkit-transform: translateY($translate-y);
    -moz-transform: translateY($translate-y);
    -ms-transform: translateY($translate-y);
    -o-transform: translateY($translate-y);
    transform: translateY($translate-y);
}

@mixin scale($scale-x, $scale-y) {
    -webkit-transform: scale($scale-x, $scale-y);
    -moz-transform: scale($scale-x, $scale-y);
    -ms-transform: scale($scale-x, $scale-y);
    -o-transform: scale($scale-x, $scale-y);
    transform: scale($scale-x, $scale-y);
}

@mixin scaleX($scale-x) {
    -webkit-transform: scaleY($scale-x);
    -moz-transform: scaleY($scale-x);
    -ms-transform: scaleY($scale-x);
    -o-transform: scaleY($scale-x);
    transform: scaleY($scale-x);
}

@mixin scaleY($scale-y) {
    -webkit-transform: scaleY($scale-y);
    -moz-transform: scaleY($scale-y);
    -ms-transform: scaleY($scale-y);
    -o-transform: scaleY($scale-y);
    transform: scaleY($scale-y);
}

@mixin animation($animation-opts) {
    -webkit-animation: $animation-opts;
    -o-animation: $animation-opts;
    animation: $animation-opts;
}

@mixin animation-delay($delay) {
    -webkit-animation-delay: $delay;
    -moz-animation-delay: $delay;
    -o-animation-delay: $delay;
    animation-delay: $delay;
}

@mixin keyframes($name) {
    @keyframes #{$name} {
        @content;
    }
    @-webkit-keyframes #{$name} {
        @content;
    }
    @-moz-keyframes  #{$name}{
        @content;
    }
    @-o-keyframes #{$name} {
        @content;
    }
}